declare global {
  interface Window {
    React: typeof React;
    ReactDOM: typeof ReactDOM;
    VUER: typeof VUER;
    THREE: typeof THREE;
    JSX: typeof JSX;
    FIBER: typeof FIBER;
    XR: typeof XR;
    Drei: typeof Drei;
  }
}

if (typeof window !== 'undefined') {
  window.React = React;
  window.ReactDOM = ReactDOM;
  window.VUER = VUER;
  window.THREE = THREE;
  window.JSX = JSX;
  window.FIBER = FIBER;
  window.XR = XR;
  window.Drei = Drei;
}

function VuerPlayback() {
  const playback = usePlayback();
  const { downlink } = useSocket();

  useEffect(() => {
    if (!playback) return;
    const cancel = [
      downlink.subscribe('SET', playback.addKeyFrame),
      downlink.subscribe('ADD', playback.addKeyFrame),
      downlink.subscribe('UPDATE', playback.addKeyFrame),
      downlink.subscribe('UPSERT', playback.addKeyFrame),
      downlink.subscribe('REMOVE', playback.addKeyFrame),
    ];

    return () => {
      cancel.forEach((f) => f());
    };
  }, [playback, downlink]);

  const combinedStream = useMemo(() => {
    if (!playback?.store) return downlink;
    return new Combine(downlink, playback.store);
  }, [downlink, playback?.store]);

  if (!playback) {
    return (
      <SceneFileDropContainer
        stream={combinedStream}
        style={{
          display: 'flex',
          flexDirection: 'row',
        }}
        innerStyle={{
          display: 'flex',
          flexDirection: 'row',
        }}
      >
        <SceneContainer
          frameloop="always"
          stream={downlink}
          bgChildren={[
            <HemisphereLightStage key="default-light-stage" />,
            <Hands key="default-hands" fps={30} stream />,
          ]}
        >
          <ContribLoader
            _key="contrib-loader-mujoco"
            library="@vuer-ai/mujoco-ts"
            version="0.0.24-rc13"
            main="dist/index.umd.js"
          />
          <Grid key="default-grid" _key="default-grid" />
        </SceneContainer>
      </SceneFileDropContainer>
    );
  } else
    return (
      <ResizableSwitch
        offset={200}
        minOffset={48}
        hidden={true}
        style={{
          width: '100%',
          height: '100%',
        }}
      >
        <SceneFileDropContainer
          stream={combinedStream}
          prompt={'scene files supported: *.urdf *.pcd *.ply *.splat'}
          style={{
            display: 'flex',
            flexDirection: 'row',
          }}
          innerStyle={{
            display: 'flex',
            flexDirection: 'row',
          }}
        >
          <SceneContainer
            frameloop="always"
            stream={combinedStream}
            bgChildren={[
              <HemisphereLightStage key="default-light-stage" />,
              <Hands key="default-hands" fps={30} stream />,
            ]}
          >
            <ContribLoader
              _key="contrib-loader-mujoco"
              library="@vuer-ai/mujoco-ts"
              version="0.0.24-rc13"
              main="dist/index.umd.js"
            />
            <Grid key="default-grid" _key="default-grid" />
          </SceneContainer>
        </SceneFileDropContainer>
        <>
          <PlaybackStateProvider>
            <TimelineStateProvider>
              <PlaybackBar />
              <TimelineFileDrop>
                <Timeline />
                {/*{clips.map((clip, i)=>{*/}
                {/*  return <Clip {...clip} onDrag onSelectStart onSelectEnd/>*/}
                {/*})}*/}
              </TimelineFileDrop>
            </TimelineStateProvider>
          </PlaybackStateProvider>
        </>
        <PlaybackStateProvider>
          <TimelineStateProvider>
            <PlaybackBar />
          </TimelineStateProvider>
        </PlaybackStateProvider>
      </ResizableSwitch>
    );
}

function SidePanelView() {
  // this is excessive. triggers when position/rotation changes too.
  // const scene = useSceneStore(({ children, bgChildren, rawChildren }) =>
  //   ({ children, bgChildren, rawChildren }));
  const children = useSceneStore(({ children }) => children);
  const sceneOps = useSceneStore(({ ops }) => ops);
  // return <VuerPlayback />;

  return (
    <Resizable vertical offset={-200} hidden={true}>
      <VuerPlayback />
      <GraphView title="Scene Graph" nodes={children} sceneOps={sceneOps} />
    </Resizable>
  );
}

export default function Page() {
  const isSSR = typeof window === 'undefined';

  return (
    <>
      <Helmet>
        <title>Vuer Editor</title>
      </Helmet>
      <main>
        <PlaybackProvider>
          {isSSR ? null : (
            <WebSocketProvider>
              {/*<VuerPlayback />*/}
              <SidePanelView />
            </WebSocketProvider>
          )}
        </PlaybackProvider>
        <Leva
          theme={{
            sizes: {
              rootWidth: '380px',
              controlWidth: '200px',
              numberInputMinWidth: '56px',
            },
          }}
        />
      </main>
    </>
  );
}
